//模拟当前用户user_id
const uid = 1;
import $T from '@/common/time.js'
export default {
	props:{
		item:Object,
		index:Number,
		pretime:Number
	},
	computed:{
		// 是否是用户登陆本人
		isSelf() {
			return uid === this.item.user_id
		},
		shortTime(){
			return $T.getChatTime(this.item.create_time,this.pretime)
		}
	}
}
